import "../css/main.css"

import Board from "./board";

const canvas = document.querySelector('canvas');
const context = canvas.getContext('2d');

canvas.width = 1024;
canvas.height = 610;

let modal = document.getElementById("level");
let board = new Board(canvas, modal);

let modalButtonEl = modal.querySelector('button');
modalButtonEl.addEventListener('click', (event) => {
    init();
});


board.showLevelBoard();


function init() {
    board.init();
    animate()

    modal.style.display = "none";
}

function animate() {
    if (!board.isGameActive()) return;

    requestAnimationFrame(animate);

    context.fillStyle = 'white';
    context.fillRect(0, 0, canvas.width, canvas.height);

    board.update();
}