export default class Keyboard {
    constructor() {
        this.enabled = true;
        this.lastKey = '';

        this.keys = {
            left: {
                pressed: false
            },
            right: {
                pressed: false
            },
            up: {
                pressed: false
            }
        }

        this.init();
    }

    init() {
        window.addEventListener('keydown', ({key}) => {
            if (!this.enabled) return;
                
            switch (key) {
                case 'ArrowLeft':
                    this.keys.left.pressed = true;
                    this.lastKey = 'left';
                    break;
                case 'ArrowRight':
                    this.keys.right.pressed = true;
                    this.lastKey = 'right';
                    break;
                case 'ArrowUp':
                    this.keys.up.pressed = true;
                    this.lastKey = 'up';
                    break;
            }
        });
        
        window.addEventListener('keyup', ({key}) => {
            switch (key) {
                case 'ArrowLeft':
                    this.keys.left.pressed = false;
                    break;
                case 'ArrowRight':
                    this.keys.right.pressed = false;
                    break;
                case 'ArrowUp':
                    this.keys.up.pressed = false;
                    break;
            }
        });
    }

    disable() {
        this.enabled = false;
    }

    enable() {
        this.enabled = true;
    }

    isEnabled() {
        return this.enabled;
    }

    getKeys() {
        return this.keys;
    }

    getLastKey() {
        return this.lastKey;
    }
}