import Utils from "./utils";

import spriteStandLeft from "../images/playerStandLeftSprite.png";
import spriteStandRight from "../images/playerStandRightSprite.png";
import spriteRunRight from "../images/playerRunRightSprite.png";
import spriteRunLeft from "../images/playerRunLeftSprite.png";
import spriteJump from "../images/playerJumpSprite.png";
import spriteJumpHitPlatform from "../images/playerJumpHitPlatformSprite.png";
import spriteLay from "../images/playerLaySprite.png";
import spriteFall from "../images/playerFallSprite.png";
import spriteFallDuck from "../images/playerFallDuckSprite.png";

export default class Player {
    constructor(canvas, keyboard) {
        this.canvas = canvas;
        this.context = canvas.getContext("2d");
        
        this.utils = new Utils();

        this.keyboard = keyboard;
        this.keys = this.keyboard.getKeys();

        this.width = 50;
        this.height = 50;
        
        this.position = {
            x: canvas.width / 2 - this.width / 2,
            y: canvas.height - this.height
        }
        this.velocity = {
            x: 0,
            y: 0
        }
        this.speed = 3;

        this.platform_nr = 0;
        this.status = 'stand';

        this.sprite = {};
        this.sprites = {
            cropWidth: 50,
            stand: {
                left: this.utils.createImage(spriteStandLeft),
                right: this.utils.createImage(spriteStandRight),
            },
            run: {
                right: this.utils.createImage(spriteRunRight),
                left: this.utils.createImage(spriteRunLeft),
            },
            jump: {
                up: this.utils.createImage(spriteJump),
                hit: this.utils.createImage(spriteJumpHitPlatform),
            },
            lay: {
                hit: this.utils.createImage(spriteLay)
            },
            fall: {
                down: this.utils.createImage(spriteFall),
                duck: this.utils.createImage(spriteFallDuck)
            } 
        }
        this.currentSprite = this.sprites.stand.left;
    }

    draw(frames) {
        this.context.drawImage(
            this.currentSprite,
            this.sprites.cropWidth * frames,
            0,
            this.sprites.cropWidth,
            50,
            this.position.x, 
            this.position.y, 
            this.width, 
            this.height
        );
    }

    update(frames) {
        this.draw(frames);

        if (this.currentSprite === this.sprites.run.right || this.currentSprite === this.sprites.run.left) {
            this.position.x += this.velocity.x;
        }
        
        this.position.y += this.velocity.y;
    }

    moveX() {
        if (this.keys.right.pressed) {
            this.velocity.x = this.speed;

            if (this.position.x > this.canvas.width) {
                this.position.x = -this.width / 2;
            }

        } else if (this.keys.left.pressed) {
            this.velocity.x = -this.speed;

            if (this.position.x + this.width < 0) {
                this.position.x = this.canvas.width - this.width / 2;
            }
        } else if (this.keys.up.pressed) {

        } else {
            this.velocity.x = 0;
        }
    }

    setStatus(status) {
        this.status = status;
    }

    getStatus() {
        return this.status;
    }
}