import Platform from "./platform";
import Utils from "../js/utils";

export default class Enemy {
    static height = 50;
    
    constructor({canvas, direction, x, y, sprite}) {
        this.canvas = canvas;
        this.context = canvas.getContext("2d");

        this.utils = new Utils();

        this.width = 50;
        this.height = 50;
        
        this.direction = direction;
        this.position = {
            x: x,
            y: y
        }
        this.velocity = {
            x: 0,
            y: 0
        }
        this.speed = 3;

        this.platform_nr = 0;

        this.frames = 0;
        this.framesCounter = 0;

        this.sprite = {
            img: sprite,
            width: 50
        }
    }

    draw() {
        this.context.drawImage(
            this.sprite.img,
            this.sprite.width * this.frames,
            0,
            this.sprite.width,
            50,
            this.position.x, 
            this.position.y, 
            this.width, 
            this.height
        );
    }

    update() {
        if (this.framesCounter > 0) {
            this.framesCounter -= 1;
        } else {
            if (this.frames > 4) {
                this.frames = 0;
            } else {
                this.frames++;
            }

            this.framesCounter = 6;
        }

        this.draw();

        if (this.direction === 'left') {
            this.position.x -= this.speed;
        } else if (this.direction === 'right') {
            this.position.x += this.speed;
        }

        // enemy goes left
        if (this.position.x + this.width < 0) {
            this.position.x = this.canvas.width;

            if (this.position.y === 35) {
                this.position.y = this.position.y + Platform.deltaHeight * 6;
            } else {
                this.position.y -= Platform.deltaHeight;
            }
        } else if (this.position.x > this.canvas.width) {
            // enemy goes right
            this.position.x = -this.width;

            if (this.position.y === Platform.deltaHeight * 7 - 40) {
                this.position.y = this.position.y - Platform.deltaHeight * 6;
            } else {
                this.position.y += Platform.deltaHeight;
            }
        }
    }
}