import Keyboard from "./keyboard";
import Platform from "./platform";
import Hole from "./hole";
import Enemy from "./enemy";
import Player from "./player";
import Utils from "./utils";
import {levels} from "./levels";

import enemyAxeSprite from "../images/enemyAxeSprite.png";
import enemyCarSprite from "../images/enemyCarSprite.png";
import enemyPlaneSprite from "../images/enemyPlaneSprite.png";
import enemyDinoSprite from "../images/enemyDinoSprite.png";
import enemyHunterSprite from "../images/enemyHunterSprite.png";
import enemyTrainSprite from "../images/enemyTrainSprite.png";
import enemyAlienSprite from "../images/enemyAlienSprite.png";
import liveImg from "../images/live.png";


const directions = [
    'left',
    'right'
];

const enemySprites = [
    'axe',
    'dino',
    'plane',
    'car',
    'hunter',
    'train',
    'alien',
];

const config = {
    platformsCount: 9,
    livesCount: 6,
}

export default class Board {
    constructor(canvas, modal) {
        this.canvas = canvas;
        this.modal = modal;
        
        this.keyboard = new Keyboard();
        this.keys = this.keyboard.getKeys();

        this.platforms = [];
        this.enemies = [];

        this.platformsCount = config.platformsCount;
        this.lives = config.livesCount;

        this.currentLevel = 0;

        this.game = {
            active: false,
            status: 'new'
        };

        this.player = new Player(canvas, this.keyboard);
        this.utils = new Utils();

        this.sprites = {
            enemy: {
                axe: this.utils.createImage(enemyAxeSprite),
                car: this.utils.createImage(enemyCarSprite),
                plane: this.utils.createImage(enemyPlaneSprite),
                dino: this.utils.createImage(enemyDinoSprite),
                hunter: this.utils.createImage(enemyHunterSprite),
                train: this.utils.createImage(enemyTrainSprite),
                alien: this.utils.createImage(enemyAlienSprite),
            },
        };

        this.spritesConfig = enemySprites;

        this.spriteCounter = 0;
        this.spriteFrames = 0;
    }

    init() {
        if (this.game.status === 'lost' || this.game.status === 'won') {
            this.lives = config.livesCount;
            this.currentLevel = 0;
        }

        this.player.status = 'stand';
        this.player.currentSprite = this.player.sprites.stand.left;

        this.level = levels[this.currentLevel];

        this.game.status = "play";
        this.game.active = true;

        this.platforms = [];
        this.enemies = [];

        this.frames = 0;
        this.spriteCounter = 0;
        this.spriteFrames = 0;

        this.player.platform_nr = 0;

        this.player.position = {
            x: this.canvas.width / 2 - this.player.width / 2,
            y: this.canvas.height - this.player.height
        }

        let deltaHeight = 0;
        for (let i = 0; i < this.platformsCount; i++) {
            this.platforms[i] = new Platform({canvas: this.canvas, x: 0, y: this.canvas.height - deltaHeight});

            deltaHeight += Platform.deltaHeight;
        }

        for (let j = 1; j <= this.level.holes_count; j++) {
            this.createNewHole();
        }

        for (let k = 1; k <= this.level.enemies_count; k++) {
            let platformNr = this.utils.randomIntFromRange(1, 7);
            let platformHeight = platformNr * Platform.deltaHeight;
            let spriteNr = k % this.spritesConfig.length;

            this.enemies.push(
                new Enemy({
                    canvas: this.canvas,
                    direction: directions[0],
                    x: this.utils.randomIntFromRange(Hole.width, this.canvas.width - 2 * Hole.width), 
                    y: this.canvas.height - platformHeight - Enemy.height,
                    sprite: this.sprites.enemy[this.spritesConfig[spriteNr]]
                })
            );
        }

        this.updateLives();
        this.updateLevelBox();
    }

    update() {       
        this.updateHoles();

        this.updateEnemies();

        this.updatePlayer();
    }

    updatePlayer() {
        let jumpsIntoHole = false;

        let previousSprite = this.player.currentSprite;

        for(let i = 0; i < this.platforms.length; i++) {
            this.platforms[i].draw();

            if (this.player.platform_nr === i) {
                // Player jumps - under upper platform - checks if there is hole or not
                if ((this.platforms[i+1] !== undefined) &&
                    (this.player.position.y === this.platforms[i+1].position.y)
                ) {
                    console.log('11111111');

                    jumpsIntoHole = false;
                    this.platforms[i+1].holes.forEach(hole => {
                        // Player jumped into hole in upper platform
                        if ((this.player.position.x > hole.position.x) &&
                            (this.player.position.x + this.player.width < hole.position.x + hole.width))
                        {
                            jumpsIntoHole = true;
                        } 
                    });
                    
                    // player jump and hit platform
                    if (!jumpsIntoHole) {
                        this.player.velocity.y = 0;

                        this.player.status = 'jump_hit_platform';
                        this.player.currentSprite = this.player.sprites.jump.hit;

                        this.frames++;
                        if (this.frames % 60 === 0) {
                            this.player.velocity.y = 5;
                            this.player.currentSprite = this.player.sprites.stand.left;

                            this.frames = 0;
                        }
                    }
                } else if ((this.platforms[i+1] !== undefined) &&
                           (this.player.position.y + this.player.height === this.platforms[i+1].position.y)
                ) {
                    console.log('2222222');
                    // player jumped into hole in upper platform
                    // stays until platform under player appear - than increase player platform
                    // player should not fall in same hole he jumped

                    this.player.velocity.y = 0;

                    if (this.platforms[i+1].holes.length > 0) {
                        this.jumpedIntoHoleCount = 0;
                        this.platforms[i+1].holes.forEach(hole => {
                            if ((this.player.position.x < hole.position.x) ||
                                (this.player.position.x + this.player.width > hole.position.x + hole.width)
                            ) {
                                this.jumpedIntoHoleCount++;
                                
                                this.player.currentSprite = this.player.sprites.stand.left;
                            }
                        });

                        if (this.jumpedIntoHoleCount === this.platforms[i+1].holes.length) {
                            this.player.platform_nr++;

                            this.createNewHole();

                            if (this.player.platform_nr === this.platforms.length - 1) {
                                this.game.active = false;
                                this.game.status = "next_level";

                                this.currentLevel++;

                                if (this.currentLevel === Object.keys(levels).length) {
                                    this.game.status = "won";
                                }

                                this.showLevelBoard();
                            }
                        }
                    }
                } else if (this.player.position.y + this.player.height === this.platforms[i].position.y) {
                    // console.log('3333333');
                    // Player stands on current platform
                    // should be checked if there is hole

                    this.platforms[i].holes.forEach(hole => {
                        if ((this.player.position.x >= hole.position.x) &&
                            (this.player.position.x + this.player.width <= hole.position.x + hole.width)
                        ) {
                            // Player falls into hole in current platform

                            this.player.status = 'fall';
                            this.player.currentSprite = this.player.sprites.fall.down;

                            this.player.velocity.y = 5;
                        }
                    });

                    if (this.player.status !== 'fall') {
                        // Player stands on platform or fall and duck or lay

                        this.player.velocity.y = 0;

                        if (this.player.status === 'fell') {
                            this.player.currentSprite = this.player.sprites.fall.duck;
                            this.player.status = 'fall_duck';
                        } else if (this.player.status === 'jump_hit_platform') {
                            this.player.currentSprite = this.player.sprites.lay.hit;
                            this.player.status = 'lay_hit';
                        }

                        if (this.keys.up.pressed) {
                            this.player.velocity.y = -5;
                        }
                    }
                } else if ((this.platforms[i-1] !== undefined) &&
                           (this.player.position.y === this.platforms[i].position.y)
                ) {
                    // console.log('44444444');
                    // Player fell into hole and top of player is below platform player stood

                } else if ((this.platforms[i-1] !== undefined) &&
                           (this.player.position.y + this.player.height === this.platforms[i-1].position.y)
                ) {
                    console.log('55555555');
                    // Player fell into hole and lands on lower platform

                    this.player.velocity.y = 0;
                    this.player.platform_nr--;

                    this.player.status = 'fell';

                    if (this.player.platform_nr === 0) {
                        this.lives--;

                        this.updateLives();

                        if (this.lives < 1) {
                            this.game.active = false;
                            this.game.status = "lost";

                            this.showLevelBoard();
                        }
                    }
                }
            }

            this.platforms[i].holes.forEach((hole) => {
                hole.update();
            });
        }

        this.updatePlayerSprite(previousSprite);

        this.player.moveX();

        this.player.update(this.spriteFrames);
    }

    createNewHole() {
        let platformNr = this.utils.randomIntFromRange(1, 8);
        let platformHeight = platformNr * Platform.deltaHeight;

        let positionY = this.canvas.height - platformHeight;
        let positionX = this.createElementPositionAndCheckIfNotOverlaps(this.platforms[platformNr].holes, Hole.width);

        if (positionX !== undefined) {
            this.platforms[platformNr].holes.push(
                new Hole({
                    canvas: this.canvas,
                    direction: directions[Math.floor(Math.random() * directions.length)],
                    x: positionX,
                    y: positionY
                })
            );
        }
    }

    createElementPositionAndCheckIfNotOverlaps(elements, width) {
        let elementsCount = elements.length;
        let x = undefined;

        let i;
        let j = 0;

        do {
            x = this.utils.randomIntFromRange(0, this.canvas.width - width);

            i = 0;
            elements.forEach((hole, index) => {
                if (x > hole.position.x + width || x + width < hole.position.x) {
                    i++;
                }
            });
            j++;

            // protection, not to overloop
            if (j === 10) {
                return undefined;
            }
        } while (i < elementsCount);

        return x;
    }

        
    updateHoles() {
        for(let i = 0; i < this.platforms.length; i++) {
            this.platforms[i].holes.forEach((hole, index) => {
                // hole goes left
                if (hole.position.x + hole.width < 0) {
                    hole.position.x = this.canvas.width;
        
                    if (hole.position.y === 10) {
                        hole.position.y = hole.position.y + Platform.deltaHeight * 7;

                        this.platforms[1].holes.push(hole);
                        this.platforms[i].holes.splice(index, 1);
                    } else {
                        // move hole to next (up) platform
                        hole.position.y -= Platform.deltaHeight;

                        this.platforms[i+1].holes.push(hole);
                        this.platforms[i].holes.splice(index, 1);
                    }
                } else if (hole.position.x > this.canvas.width) {
                    // hole goes right
                    hole.position.x = -hole.width;
        
                    if (hole.position.y === Platform.deltaHeight * 7 + 10) {
                        hole.position.y = hole.position.y - Platform.deltaHeight * 7;

                        this.platforms[this.platforms.length-1].holes.push(hole);
                        this.platforms[i].holes.splice(index, 1);
                    } else {
                        hole.position.y += Platform.deltaHeight;

                        this.platforms[i-1].holes.push(hole);
                        this.platforms[i].holes.splice(index, 1);
                    }
                }
            });
        }
    }

    updateEnemies() {
        this.enemies.forEach(enemy => {
            if ((enemy.position.y === this.player.position.y) &&
                ((enemy.position.x < this.player.position.x && enemy.position.x + enemy.width > this.player.position.x) ||
                 (enemy.position.x < this.player.position.x + this.player.width && enemy.position.x + enemy.width > this.player.position.x + this.player.width))
            ) {
                if (this.player.currentSprite !== this.player.sprites.lay.hit) {
                    this.spriteFrames = 0;
                    this.spriteCounter = 0;
                }
                this.player.currentSprite = this.player.sprites.lay.hit;
            }

            enemy.update();
        });
    }

    updatePlayerSprite(previousSprite) {
        let lastKey = this.keyboard.getLastKey();

        if ((this.keys.right.pressed && lastKey === 'right') &&
            (this.player.currentSprite !== this.player.sprites.run.right) &&
            (this.player.currentSprite !== this.player.sprites.lay.hit) &&
            (this.player.currentSprite !== this.player.sprites.fall.down) &&
            (this.player.currentSprite !== this.player.sprites.fall.duck)
        ) {
            this.player.currentSprite = this.player.sprites.run.right;
        } else if ((this.keys.left.pressed && lastKey === 'left') &&
                   (this.player.currentSprite !== this.player.sprites.run.left) &&
                   (this.player.currentSprite !== this.player.sprites.lay.hit) &&
                   (this.player.currentSprite !== this.player.sprites.fall.down) &&
                   (this.player.currentSprite !== this.player.sprites.fall.duck)
        ) {
            this.player.currentSprite = this.player.sprites.run.left;
        } else if ((!this.keys.right.pressed && lastKey === 'right') && 
            (this.player.currentSprite !== this.player.sprites.stand.right) && 
            (this.player.currentSprite !== this.player.sprites.lay.hit) &&
            (this.player.currentSprite !== this.player.sprites.fall.down) &&
            (this.player.currentSprite !== this.player.sprites.fall.duck)
        ) {
            this.player.currentSprite = this.player.sprites.stand.right;
        } else if (!this.keys.left.pressed && lastKey === 'left' &&
             this.player.currentSprite !== this.player.sprites.stand.left && 
            (this.player.currentSprite !== this.player.sprites.lay.hit) &&
            (this.player.currentSprite !== this.player.sprites.fall.down) &&
            (this.player.currentSprite !== this.player.sprites.fall.duck)
        ) {
            this.player.currentSprite = this.player.sprites.stand.left;
        } else if (this.keys.up.pressed) {
            this.frames = 0;
            this.player.currentSprite = this.player.sprites.jump.up;
        } else if (this.player.currentSprite === this.player.sprites.fall.down) {

        } else if (this.player.currentSprite === this.player.sprites.fall.duck) {
            this.frames++;
            if (this.frames % 10 === 0) {
                this.player.currentSprite = this.player.sprites.lay.hit;
                this.frames = 0;
            }
        } else if (this.player.currentSprite === this.player.sprites.lay.hit) {
            this.frames++;
            if (this.frames % 120 === 0) {
                this.player.currentSprite = this.player.sprites.stand.left;
                this.frames = 0;
            }
        }
        
        switch (this.player.currentSprite) {
            case this.player.sprites.stand.left :
            case this.player.sprites.stand.right :
                this.player.sprite = {
                    frames_count: 2,
                    counter_count: 32,
                    last_frame_fixed: false
                }
                this.keyboard.enable();
                break;
            case this.player.sprites.run.left :
            case this.player.sprites.run.right :
                this.player.sprite = {
                    frames_count: 4,
                    counter_count: 1,
                    last_frame_fixed: false
                }
                this.keyboard.enable();
                break;
            case this.player.sprites.jump.up :
                this.player.sprite = {
                    frames_count: 0,
                    counter_count: 2,
                    last_frame_fixed: true
                }
                this.keyboard.disable();
                break;
            case this.player.sprites.jump.hit :
                this.player.sprite = {
                    frames_count: 1,
                    counter_count: 32,
                    last_frame_fixed: true
                }
                this.keyboard.disable();
                break;
            case this.player.sprites.fall.down :
                this.player.sprite = {
                    frames_count: 1,
                    counter_count: 4,
                    last_frame_fixed: true
                }
                this.keyboard.disable();
                break;
            case this.player.sprites.fall.duck :
                this.player.sprite = {
                    frames_count: 0,
                    counter_count: 0,
                    last_frame_fixed: true
                }
                this.keyboard.disable();
                break;
            case this.player.sprites.lay.hit :
                this.player.sprite = {
                    frames_count: 0,
                    counter_count: 4,
                    last_frame_fixed: false
                }
                this.keyboard.disable();
                break;
        }
 
        if (previousSprite !== this.player.currentSprite) {
            this.spriteFrames = 0;
            this.spriteCounter = 0;
        }

        if (this.spriteCounter > 0) {
            this.spriteCounter -= 1;
        } else {
            if (this.spriteFrames === this.player.sprite.frames_count && this.player.sprite.last_frame_fixed) {
                this.spriteFrames = this.player.sprite.frames_count;
            } else if (this.spriteFrames > this.player.sprite.frames_count) {
                this.spriteFrames = 0;
            } else {
                this.spriteFrames++;
            }
            
            this.spriteCounter = this.player.sprite.counter_count;
        }
    }

    updateLives() {
        this.livesEl = document.querySelector("#lives");
        this.livesEl.innerHTML = '';

        for (let i=1; i <= this.lives; i++) {
            this.livesEl.append(this.utils.createImage(liveImg));
        }
    }

    updateLevelBox() {
        this.levelEl = document.querySelector("#current_level");
        this.levelEl.innerHTML = 'LEVEL - ' + this.currentLevel;
    }

    showLevelBoard() {
        this.modal.style.display = "block";

        this.game.active = false;

        let modalDescEl = this.modal.querySelector('div.description');
        let modalButtonTextEl = this.modal.querySelector('button');

        let desc = '';
        let buttonText = '';

        switch (this.currentLevel) {
            case 0:
            case 1:
            case 2:
            case 3:
            case 4:
            case 5:
                desc = "NEXT LEVEL - " + this.currentLevel;
                buttonText = "PLAY";
                break;
            default:
                desc = "LEVEL 0";
                buttonText = "PLAY";
        }

        switch (this.game.status) {
            case 'lost':
                desc = "You lost";
                buttonText = "PLAY AGAIN";
                break;
            case 'won':
                desc = "You won";
                buttonText = "PLAY AGAIN";
                break;     
        }

        modalDescEl.innerHTML = desc;
        modalButtonTextEl.innerHTML = buttonText;
    }

    isGameActive() {
        return this.game.active;
    }
}